
export const cargarImagen = async (path) => {
  if (!path) {
    return null;
  }

  // Limpia la ruta: reemplaza barras invertidas y elimina barras duplicadas.
  const cleanedPath = path.replace(/\\/g, '/').replace(/\/{2,}/g, '/');
  
  // Construye la URL completa anteponiendo la URL base de la API.
  const imageUrl = `${process.env.REACT_APP_API_URL}${cleanedPath}`;

  // Verifica si la URL es una imagen vlida y accesible.
  if (await verificarImagen(imageUrl)) {
    return imageUrl;
  }
  
  return null;
};


/**
 * Verifica si una imagen existe en una URL.
 * @param {string} url - URL de la imagen a verificar.
 * @returns {Promise<boolean>} - true si la imagen existe, false si no.
 */
const verificarImagen = async (url) => {
  if (!url) return false;
  try {
    const response = await fetch(url, { method: 'HEAD' });
    // Verifica que la respuesta sea exitosa y que el tipo de contenido sea imagen
    return response.ok && response.headers.get('content-type')?.startsWith('image');
  } catch (error) {
    // Es normal que falle si el recurso no existe, no es necesario registrar el error.
    return false;
  }
}

/**
 * Retorna la URL de la imagen vlida entre la principal y la alternativa.
 * @param {string} urlPrincipal - URL de la imagen principal.
 * @param {string} urlAlternativa - URL de la imagen alternativa.
 * @returns {Promise<string|null>} - URL vlida o null si ninguna existe.
 */
export const obtenerImagenValida = async (urlPrincipal, urlAlternativa) => {
  if (await verificarImagen(urlPrincipal)) {
    return urlPrincipal;
  } else if (await verificarImagen(urlAlternativa)) {
    return urlAlternativa;
  } else {
    return null;
  }
}
